.main {
  /* margin-top: 4rem; */
  height: 100%;
  background-color: #f6f6f6;
  width: 100%;
  font-family: Verdana, blutos, Tahoma, sans-serif;
  overflow: auto
  ;
}

.plancontainer {
  padding: 20px 20px;
  box-sizing: border-box;
  width: 100%;
  display: flex; 
  flex-wrap: wrap;
  flex-direction: row;
}
.plan {
  background-color: #ffffff;
text-align: center;
width: 100%;
  border-radius: 15px;
  margin:10px auto;
  box-shadow: 0 5px 15px 0 rgb(110 110 110 / 10%);
}
.planheader {
  padding: 20px 30px;
}
.plandetails {
  list-style: none; 
  background-color: #1e88e5;
  text-align: center;
  color: #DBEAFB;
  padding: 10px 30px;
  font-size: 16px;
}
.plandetails li{
  padding-top: 2rem;
}
.plancta {
}

@media only screen and (min-width: 576px) {
  .plancontainer {
    max-width: 540px;
    /* flex-direction: row; */
  }
 .plan{
  max-width: 50%;
 }
 
}
@media only screen and (min-width: 768px) {
  .plancontainer {
    max-width: 700px;
  }
 
 
}
