.header {
  box-sizing: border-box;
  width: 100%;
  height: 4rem;
  
  background-color: #417690;
  /* color: rgb(219, 220, 227); */
  padding: 0 20px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}
.liholder{
  display: inline-block;
}
.header nav {
  width: 90%;

  display: inline-block;
}
/* aghaaaaa ul automatic padding lefte 40 dare va margin bala va pain. */
.header ul {
  overflow-x: auto;
  max-width: 100%;
  list-style: none;
  padding: 0;
margin: 0;
  display: block;
  white-space: nowrap;
  text-align: center;
}


.header li {
  margin: 0 1rem;
  display: inline-block;
  white-space: nowrap;
 font-size: 1.2rem;
}

.header a {
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-weight: bold;
  display: inline-block;
}

.header a:hover,
.header a:active,
.header a.active {
  color: white;
  padding-bottom: 0.25rem;
  border-bottom: 4px solid #ffffff;
}

.menu {
  cursor: pointer;
}

.leitner {
  background-color: #00aeff;
  color: white;
}
.users {
  background-color: rgb(80, 79, 79);
}
.users a {
  color: rgb(206, 205, 205);
}


