.form {
  display: flex;
  flex-direction: column;
  margin: 0 auto;

  background-color: white;

  padding: 1rem;

  color: #5a595a;
  border-radius: 2.75rem;
  max-width: 400px;
  text-decoration: none;
  box-shadow: 1px 2px 10px #2020231a;
  padding-bottom: 0.5rem;
}
.form a {
  color: inherit;
}
.buttoncontainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 0;
}

.form label {
  margin-top: 1rem;
  color: #5a595a;
}
.form input {
  margin-top: 5px;
  height: 2rem;
  border-radius: 5px;

  border: 1px solid #ced4da;
  outline: none;
  padding: 0 5px;
  color: #6d8094;

  line-height: 1.5;
}

.form button {
  font-weight: bold;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 25px;
  width: 100%;
  height: 3rem;

  background-color: green;
  color: #ffffff;
  font-size: inherit;
}

form button:hover {
  cursor: pointer;
  background-color: rgb(22, 37, 241);
  color: #8ecae6;
}
.vaselink a {
  color: green;
  font-weight: bold;
}
