.kol{
    position:fixed;
    background-color: rgba(0, 0, 0, 0.5);
    top:0;
    left:0;
    width: 100%;
    z-index: 6;
}

.sidebar{
    height:100vh;
    max-width: 28%;
    background-color: rgb(34, 34, 34);
    z-index: 5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.1s ease-out;
}
.sidebarshow{  transform: translateX(0%);}
.sidebarclosed { 
  transform: translateX( -100%)
}


.sidebar nav {
    height: 80%;
  
    margin-top: 1rem;
    display: flex;
    flex-direction: column; 
    justify-content: center;
 
}

.sidebar ul {
      overflow: auto;
    /* height: 100%; */
    width: 100%;
    list-style: none;
    padding: 0;
    margin:0 auto;

  }

  
  .sidebar li {
    margin: 0 1rem;
   display: block;
    min-height: 4rem;
    
  }
  
  .sidebar a {
    color: white;
    text-decoration: none;
    display: block;
    text-align: center; 

  }
  
  .sidebar a:hover,
  .sidebar a:active,
  .sidebar a.active {
    color: #457b9d;
    padding-bottom: 0.25rem;
    border-bottom: 4px solid #457b9d;
  }

  @media only screen and (min-width: 900px) {
  
    .sidebar{
       
      max-width: 10%; 
  }
  }

 

  .leitner{
    background-color:#00aeff ;
  }
  .users{
      
    background-color: rgb(48, 48, 48);
  }
  .users a{
      color: rgb(206, 205, 205);
  }